body {
  width: 100%;
  height: 100vh;
  margin: 0;
}

.loaderWrapper {
  width: 100%;
  margin: 25px auto;
}

.loader {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50px;
  height: 50px;
  background-color: #ff0000;
  border-radius: 50%;
}

.loader:after {
  content: '';
  position: absolute;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  border: 0px solid white;
  transform: translate(-50%, -50%);
  animation: loading 1000ms ease-out forwards infinite;
}

@keyframes loading {
  0% {
    border: 0px solid white;
  }
  20% {
    border: 8px solid white;
    width: 0%;
    height: 0%;
  }
  100% {
    border: 8px solid white;
    width: 100%;
    height: 100%;
  }
}
